<template>
    <div id="setupvue" style="width: 100%; height: 100%; position: relative;">
        <v-row style="height: 100%;">
            <v-col cols="6" sm="6" md="6" lg="6" class="pl-6 pt-0 py-2" :align-self="'center'">
                <v-icon large color="#e1e1e1">mdi-arrow-left</v-icon>
            </v-col>
            <v-col cols="6" sm="6" md="6" lg="6" class="pr-6 pt-0 py-2 text-right" :align-self="'center'">
                <v-icon large color="#e1e1e1">mdi-close-circle</v-icon>
            </v-col>

            <v-col cols="12" sm="12" md="12" lg="12" class="py-1">
                <v-divider></v-divider>
            </v-col>

            <v-col cols="2" sm="2" md="2" lg="2">
                <!-- <v-btn icon large @click="prev">
                    <v-icon large>mdi-chevron-double-left</v-icon>
                </v-btn> -->
            </v-col>
            <v-col cols="8" sm="8" md="8" lg="8">
                <v-window v-model="onboarding" :continuous="true">
                    <v-window-item v-for="n in length" :key="`card-${n}`">
                        <div v-if="n == 1">
                            <v-col cols="12" sm="12" md="12" lg="12" align="center" justify="center" class="py-5">
                                <h1>학원 정보를 확인해주세요.</h1>
                                <h2><small class="grey--text">가입 시 기재해주신 학원 정보입니다.</small></h2>
                            </v-col>

                            <v-row>
                                <v-col cols="4" sm="4" md="3" lg="3" class="text-right" :align-self="'center'">
                                    선생님 존함
                                </v-col>
                                <v-col cols="8" sm="8" md="7" lg="7" style="position: relative;">
                                    <v-btn
                                    rounded filled dense block elevation="0"
                                    color="#e9e9e9" disabled
                                    >
                                        <b class="black--text">{{ president }}</b>
                                    </v-btn>
                                    <v-icon
                                    id="checkbtn1"
                                    color="success"
                                    style="position: absolute; right: 20px; top: 18px;"
                                    @click="checkInfo(1)"
                                    >
                                    mdi-check-circle-outline
                                    </v-icon>
                                </v-col>

                                <v-col cols="4" sm="4" md="3" lg="3" class="text-right" :align-self="'center'">
                                    휴대폰 번호
                                </v-col>
                                <v-col cols="8" sm="8" md="7" lg="7" style="position: relative;">
                                    <v-btn
                                    rounded filled dense block elevation="0"
                                    color="#e9e9e9" disabled
                                    >
                                        <b class="black--text">{{ phone }}</b>
                                    </v-btn>
                                    <v-icon
                                    id="checkbtn2"
                                    color="success"
                                    style="position: absolute; right: 20px; top: 18px;"
                                    @click="checkInfo(2, this)"
                                    >
                                    mdi-check-circle-outline
                                    </v-icon>
                                </v-col>

                                <v-col cols="4" sm="4" md="3" lg="3" class="text-right" :align-self="'center'">
                                    학원 이름
                                </v-col>
                                <v-col cols="8" sm="8" md="7" lg="7" style="position: relative;">
                                    <v-btn
                                    rounded filled dense block elevation="0"
                                    color="#e9e9e9" disabled
                                    >
                                        <b class="black--text">{{ cname }}</b>
                                    </v-btn>
                                    <v-icon
                                    id="checkbtn3"
                                    color="success"
                                    style="position: absolute; right: 20px; top: 18px;"
                                    @click="checkInfo(3)"
                                    >
                                    mdi-check-circle-outline
                                    </v-icon>
                                </v-col>
                            </v-row>
                        </div>

                        <div v-if="n == 2">
                            <v-col cols="12" sm="12" md="12" lg="12" align="center" justify="center" class="py-5">
                                <h1>학원 로고를 적용해주세요.</h1>
                                <h2><small class="grey--text">온라인 학습에 삽입될 학원 로고를 사진 선택 or 촬영해주세요.</small></h2>
                                <h2><small class="grey--text">학원 로고는 초기화면의 </small><small class="success--text">[학원정보]</small><small class="grey--text">에서 수정할 수 있습니다.</small></h2>
                            </v-col>

                            <v-row align="center" justify="center">
                                <v-col cols="12" sm="8" md="8" lg="7" class="text-center">
                                    <div style="border: 2px solid lightgray; border-radius: 4px;">

                                        <v-img
                                        src="@/assets/logo1.png"
                                        aspect-ratio="1"
                                        class="my-8"
                                        contain height="80"
                                        >
                                        </v-img>

                                        <div class="text-right mb-2">
                                            <v-btn class="mx-2" fab small elevation="0" style="color: rgba(0, 0, 0, 0.26) !important; background: rgba(0, 0, 0, 0.12) !important;" @click="changeLogoConfirm">
                                                <v-icon dark>mdi-camera</v-icon>
                                            </v-btn>
                                        </div>
                                    </div>
                                </v-col>
                            </v-row>
                        </div>

                        <div v-if="n == 3">
                            <v-col cols="12" sm="12" md="12" lg="12" align="center" justify="center" class="py-5">
                                <h1>선생님의 수업 정보를 선택해주세요.</h1>
                                <h2><small class="grey--text">수업 스케쥴 설정을 위한 정보입니다.</small></h2>
                            </v-col>

                            <v-row>
                                <v-col cols="3" sm="3" md="3" lg="3" align="center" justify="center">
                                    <h3><b>교습 형태</b></h3>
                                    <v-btn
                                    v-for="i in 4"
                                    :key="`vbtn-1-${i}`"
                                    :id="`cci_btn_1_${i}`"
                                    rounded outlined dense depressed block color="grey" class="my-4 cci1"
                                    @click="checkClassInfo(1, i)"
                                    >
                                        <span v-if="i == 1">개인 수업</span>
                                        <span v-if="i == 2">공부방</span>
                                        <span v-if="i == 3">교습소</span>
                                        <span v-if="i == 4">학원</span>
                                    </v-btn>
                                </v-col>

                                <v-col cols="3" sm="3" md="3" lg="3" align="center" justify="center">
                                    <h3><b>학생 인원</b></h3>
                                    <v-btn
                                    v-for="i in 4"
                                    :key="`vbtn-2-${i}`"
                                    :id="`cci_btn_2_${i}`"
                                    rounded outlined dense depressed block color="grey" class="my-4 cci2"
                                    @click="checkClassInfo(2, i)"
                                    >
                                        <span v-if="i == 1">5명 미만</span>
                                        <span v-if="i == 2">10 ~ 15명</span>
                                        <span v-if="i == 3">15 ~ 30명</span>
                                        <span v-if="i == 4">30명 초과</span>
                                    </v-btn>
                                </v-col>

                                <v-col cols="3" sm="3" md="3" lg="3" align="center" justify="center">
                                    <h3><b>대상 학년</b></h3>
                                    <v-btn
                                    v-for="i in 4"
                                    :key="`vbtn-3-${i}`"
                                    :id="`cci_btn_3_${i}`"
                                    rounded outlined dense depressed block color="grey" class="my-4 cci3"
                                    @click="checkClassInfo(3, i)"
                                    >
                                        <span v-if="i == 1">초등</span>
                                        <span v-if="i == 2">중등</span>
                                        <span v-if="i == 3">고등</span>
                                        <span v-if="i == 4">고등 이상</span>
                                    </v-btn>
                                </v-col>

                                <v-col cols="3" sm="3" md="3" lg="3" align="center" justify="center">
                                    <h3><b>수업 시수</b></h3>
                                    <v-btn
                                    v-for="i in 4"
                                    :key="`vbtn-4-${i}`"
                                    :id="`cci_btn_4_${i}`"
                                    rounded outlined dense depressed block color="grey" class="my-4 cci4"
                                    @click="checkClassInfo(4, i)"
                                    >
                                        <span v-if="i == 1">주 1회</span>
                                        <span v-if="i == 2">주 3회</span>
                                        <span v-if="i == 3">주 5회</span>
                                        <span v-if="i == 4">기타</span>
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </div>

                        <div v-if="n == 4">
                            <v-col cols="12" sm="12" md="12" lg="12" align="center" justify="center" class="py-5">
                                <h1>온라인 학습 설정을 선택해주세요.</h1>
                                <h2><small class="grey--text">선생님의 수업에 맞게 학습 내용을 설정해주세요.</small></h2>
                                <h2>
                                    <small class="grey--text">학습 설정은 초기화면의&nbsp;</small>
                                    <small class="success--text">[관리자 정보]&nbsp;</small>
                                    <small class="grey--text">-&nbsp;</small>
                                    <small class="success--text">[학습설정]</small>
                                    <small class="grey--text">에서 수정 가능합니다.</small>
                                </h2>
                            </v-col>

                            <v-row
                            v-for="i in 4"
                            :key="`learning-setting-${i}`"
                            class="my-2"
                            >
                                <v-col cols="12" sm="4" md="4" lg="4">
                                    <h3 v-if="i==1"><b>교재 순서</b></h3>
                                    <h3 v-if="i==2"><b>학습 단계</b></h3>
                                    <h3 v-if="i==3"><b>채점 여부</b></h3>
                                    <h3 v-if="i==4"><b>듣기 학습</b></h3>
                                </v-col>
                                <v-col cols="6" sm="4" md="4" lg="4">
                                    <v-btn
                                    :class="`cls${i}`"
                                    :id="`cls_btn_${i}_1`"
                                    outlined rounded block depressed color="grey" @click="checkLearning(i, 1)"
                                    >
                                        <span v-if="i==1">순서대로 학습 <small>(권장)</small></span>
                                        <span v-if="i==2">학습단계 스킵 불가 <small>(권장)</small></span>
                                        <span v-if="i==3">실시간 채점 <small>(권장)</small></span>
                                        <span v-if="i==4">학습자 제어 가능 <small>(권장)</small></span>
                                    </v-btn>
                                </v-col>
                                <v-col cols="6" sm="4" md="4" lg="4">
                                    <v-btn
                                    :class="`cls${i}`"
                                    :id="`cls_btn_${i}_2`"
                                    outlined rounded block depressed color="grey" @click="checkLearning(i, 2)"
                                    >
                                        <span v-if="i==1">랜덤 순서대로 학습</span>
                                        <span v-if="i==2">학습단계 스킵 가능</span>
                                        <span v-if="i==3">학습 후 일괄 채점</span>
                                        <span v-if="i==4">관리자 직접 설정</span>
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </div>

                        <!-- <v-card color="transparent" height="200">
                            <v-row class="fill-height" align="center" justify="center">
                                Transparent themed, for background-imaged slides. Background color black added for demonstration purposes.
                            </v-row>
                        </v-card> -->
                    </v-window-item>
                </v-window>
            </v-col>
            <v-col cols="2" sm="2" md="2" lg="2" class="text-right pr-6" :align-self="'center'">
                <v-btn icon large @click="next" :disabled="disabled">
                    <v-icon large color="grey">mdi-chevron-double-right</v-icon>
                </v-btn>
            </v-col>

            <v-col cols="12" sm="12" md="12" lg="12" :align-self="'end'">
                <v-item-group v-model="onboarding" class="text-center" mandatory>
                    <v-item v-for="n in length" :key="`btn-${n}`" v-slot:default="{ active }">
                        <v-btn :input-value="active" icon disabled x-small color="#e9e9e9" class="mx-1">
                            <v-icon>mdi-record</v-icon>
                        </v-btn>
                    </v-item>
                </v-item-group>
            </v-col>

        </v-row>

        <Dialogs>
            <div slot="change_img">
                <v-card-text class="text-center">
                    <v-row>
                        <v-col class="px-8 text-right">
                            <v-btn color="grey" fab x-large dark style="height: 72px !important;">
                                <v-icon>mdi-folder-multiple-image</v-icon>
                            </v-btn>
                            <br />
                            <div class="mr-5 mt-4"><b>갤러리</b></div>
                        </v-col>
                        <v-divider vertical></v-divider>
                        <v-col class="px-8 text-left">
                            <v-btn color="grey" fab x-large dark style="height: 72px !important;">
                                <v-icon>mdi-camera</v-icon>
                            </v-btn>
                            <br />
                            <div class="ml-5 mt-4"><b>카메라</b></div>
                        </v-col>
                    </v-row>
                </v-card-text>

                <!-- Two Button -->
                <v-card-actions class="pa-0" style="border-top: 1px solid lightgray;">
                    <v-btn x-large class="ml-0" text style="width:100%;" @click="closeDialog">
                        <b>기본 로고로 설정</b>
                    </v-btn>
                </v-card-actions>
            </div>
        </Dialogs>

    </div>
    <!-- <v-card color="black" dark flat tile>

        <v-card-actions class="justify-space-between">


        </v-card-actions>
    </v-card> -->
</template>

<style lang="scss">
@import '@/assets/scss/setup.scss';
</style>

<script>
import Dialogs from "@/components/common/Dialogs"

export default {
    components : {
        Dialogs
    },
    data: () => ({
        length: 4,
        onboarding: 0,

        president: '',
        phone: '',
        cname : '',

        logofile: '',

        // 수업정보
        teaching_form: '',
        student_cnt: '',
        target_grade: '',
        lesson_cnt: '',

        // 학습설정
        is_random: '',
        is_skip: '',
        is_scoring: '',
        is_controllable: '',

        disabled: true,
        cis1:0, cis2:0, cis3:0, // check info status
        ccs1:0, ccs2:0, ccs3:0, ccs4:0, // check classinfo status
        cls1:0, cls2:0, cls3:0, cls4:0, // check learning settings status
    }),
    created (){
        if( !this.$storage.has('user') ){
            this.$router.push({name: 'login'});
        }

        this.president = this.$storage.get('user').president;
        this.phone = this.$storage.get('user').phone;
        this.cname = this.$storage.get('user').cname;
    },
    watch : {
        cis1(val) {
            if( this.cis1 == 1 && this.cis2 == 1 && this.cis3 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        cis2(val) {
            if( this.cis1 == 1 && this.cis2 == 1 && this.cis3 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        cis3(val) {
            if( this.cis1 == 1 && this.cis2 == 1 && this.cis3 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        ccs1(val){
            if( this.ccs1 == 1 && this.ccs2 == 1 && this.ccs3 == 1 && this.ccs4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        ccs2(val){
            if( this.ccs1 == 1 && this.ccs2 == 1 && this.ccs3 == 1 && this.ccs4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        ccs3(val){
            if( this.ccs1 == 1 && this.ccs2 == 1 && this.ccs3 == 1 && this.ccs4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        ccs4(val){
            if( this.ccs1 == 1 && this.ccs2 == 1 && this.ccs3 == 1 && this.ccs4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        cls1(val){
            if( this.cls1 == 1 && this.cls2 == 1 && this.cls3 == 1 && this.cls4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        cls2(val){
            if( this.cls1 == 1 && this.cls2 == 1 && this.cls3 == 1 && this.cls4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        cls3(val){
            if( this.cls1 == 1 && this.cls2 == 1 && this.cls3 == 1 && this.cls4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
        cls4(val){
            if( this.cls1 == 1 && this.cls2 == 1 && this.cls3 == 1 && this.cls4 == 1 ) this.disabled = false;
            else this.disabled = true;
        },
    },
    methods: {
        next() {
            // 마지막에 값 넘기고 인덱스로 넘어가기
            if( this.onboarding == 3 ){

                let promise = new Promise((resolve, reject) => {
                    this.$axios.post(`${this.$host}/academy/` + this.$cid + '/addinfo', {
                        teaching_form : this.teaching_form,
                        student_cnt : this.student_cnt,
                        target_grade : this.target_grade,
                        lesson_cnt : this.lesson_cnt,
                    }).then((res) => {
                        if(res.status == 200)
                            resolve("addinfo_success");
                    }).catch((err) => {  reject(err);  });
                });

                promise.then((res) => {
                    if(res == "addinfo_success"){
                        let promise1 = new Promise((resolve, reject) => {
                            this.$axios.post(`${this.$host}/academy/` + this.$cid + '/modifyconfig', {
                                is_random : this.is_random,
                                is_skip : this.is_skip,
                                is_scoring : this.is_scoring,
                                is_controllable : this.is_controllable,
                                configdate: true
                            }).then((res) => {
                                if(res.status == 200)
                                    resolve("modify_config_success");
                            }).catch((err) => { reject(err);  });
                        });

                        promise1.then((res) => {
                            if(res == "modify_config_success"){
                                this.$router.push({name: 'index'});
                            }
                        }).catch((err) => {
                            console.log(err);
                        })
                    }
                }).catch((err) => {
                    console.log(err);
                });

            }
            else{
                this.onboarding = this.onboarding + 1 === this.length ?
                    0 :
                    this.onboarding + 1
                if( this.onboarding == 1 ){
                    this.disabled = false;
                }else{
                    this.disabled = true;
                }
            }
        },
        // prev() {
        //     this.onboarding = this.onboarding - 1 < 0 ?
        //         this.length - 1 :
        //         this.onboarding - 1
        //     console.log( this.onboarding );
        // },
        checkInfo(idx) {
            let checkbtn = document.getElementById('checkbtn' + idx);

            if( checkbtn.classList.contains('mdi-check-circle-outline') ){
                checkbtn.classList.remove('mdi-check-circle-outline')
                checkbtn.classList.add('mdi-check-circle')
                this['cis' + idx] = 1;
            }else{
                checkbtn.classList.remove('mdi-check-circle')
                checkbtn.classList.add('mdi-check-circle-outline')
                this['cis' + idx] = 0;
            }
        },
        changeLogoConfirm () {
            this.openDialog('change_img');
        },
        checkClassInfo(bignum, midnum) {
            let bigcategory = document.getElementsByClassName("cci" + bignum);
            for(let i=0; i<bigcategory.length; i++){
                if( bigcategory[i].classList.contains("selected-setting") ){
                    bigcategory[i].classList.remove("selected-setting");
                    bigcategory[i].classList.add("grey--text");
                }
            }

            let cibtn = document.getElementById('cci_btn_' + bignum + '_' + midnum );
            cibtn.classList.add("selected-setting");
            this['ccs' + bignum] = 1;

            if(bignum == 1){
                this.teaching_form = cibtn.innerText;
            }else if(bignum == 2){
                this.student_cnt = cibtn.innerText;
            }else if(bignum == 3){
                this.target_grade = cibtn.innerText;
            }else if(bignum == 4){
                this.lesson_cnt = cibtn.innerText;
            }

        },
        checkLearning(bignum, midnum) {
            let bigcategory = document.getElementsByClassName("cls" + bignum);
            for(let i=0; i<bigcategory.length; i++){
                if( bigcategory[i].classList.contains("selected-setting") ){
                    bigcategory[i].classList.remove("selected-setting");
                    bigcategory[i].classList.add("grey--text");
                }
            }

            let clsbtn = document.getElementById('cls_btn_' + bignum + '_' + midnum );
            clsbtn.classList.add("selected-setting");
            this['cls' + bignum] = 1;

            if(bignum == 1){
                if(midnum == 1) this.is_random = 0;
                else if(midnum == 2) this.is_random = 1;
            }else if(bignum == 2){
                if(midnum == 1) this.is_skip = 0;
                else if(midnum == 2) this.is_skip = 1;
            }else if(bignum == 3){
                if(midnum == 1) this.is_scoring = 0;
                else if(midnum == 2) this.is_scoring = 1;
            }else if(bignum == 4){
                if(midnum == 1) this.is_controllable = 0;
                else if(midnum == 2) this.is_controllable = 1;
            }

        }
    },
}
</script>
